import React from 'react'
import skala from './Skala.svg'
import efeldsvg from './Efeld.svg'

export default function Simulation(props) {
  let magnetfeldZeichenAbstandFilter = 3 / Math.sqrt(props.Bf)
  let magnetfeldZeichenAbstandAnalysator = 3 / Math.sqrt(props.Ba)
  let efeldZeichenAbstand = Math.ceil(5 /Math.sqrt(props.Ef))

  return (
    <div className="ms-simulation">
      {props.particles.map((p, i) => (
        <div
          key={i}
          className="point"
          style={{
            left: p.x,
            top: p.y,
            backgroundColor: p.t
          }}
        ></div>
      ))}
      <div className="blendeOben"></div>
      <div className="blendeUnten"></div>
      {props.Bf > 0 && (
        <div className="magnetfeldZeichen">
          {[...Array(300)].map((e, i) => (
            <div
              key={i}
              style={{
                margin: magnetfeldZeichenAbstandFilter
              }}
            ></div>
          ))}
        </div>
      )}

      {props.Ef > 0 && (
        <div className="efeldZeichen">
          {[...Array(30)].map((e, i) => (
            <div
              key={i}
              style={{
                marginRight: efeldZeichenAbstand,
                marginLeft: efeldZeichenAbstand,
              }}
            ><img src={efeldsvg} alt=""/></div>
          ))}
        </div>
      )}
      <div className="kondensator kondensatorOben"></div>
      <div className="kondensator kondensatorUnten"></div>
      <div className="ionenQuelle"></div>

      <div className="blendeUnten blendeAnalysatorUnten "></div>
      <div className="schirm"></div>
      <div className="skala">
        <img src={skala} alt="Skala" />
      </div>
      <div className="analysator">
        {props.Ba > 0 && (
          <div className="magnetfeldZeichen">
            {[...Array(500)].map((e, i) => (
              <div
                key={i}
                style={{
                  margin: magnetfeldZeichenAbstandAnalysator
                }}
              ></div>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
