import React, { useState, useEffect } from 'react'
import Simulation from './Simulation'
import './App.css'

function App() {
  const [active, setActive] = useState(false)
  const [time, setTime] = useState(0)
  const [particles, setParticles] = useState([])
  const [Ef, setEf] = useState(0)
  const [Bf, setBf] = useState(0)
  const [Ba, setBa] = useState(0)

  const reset = () => {
    setParticles([])
  }

  useEffect(() => {
    reset()
  }, [Ef, Bf, Ba])

  useEffect(() => {
    const interval = setInterval(() => {
      if (!active) return
      setTime(time => time + 1)
      if (time % 8 === 0) {
        let m
        let t
        let r = Math.random()
        if (r < 0.4) {
          m = 1.23457
          t = 'gray'
        } else if (r < 0.8) {
          m = 0.6
          t = 'orange'
        } else if (r < 1) {
          m = 2.5
          t = 'black'
        }
        setParticles(
          particles.concat({
            x: -40,
            y: 150,
            vx: 2 + 1.2 * (Math.random() - 0.5),
            vy: 0,
            m: m,
            t: t,
            passedFilter: false
          })
        )
      }

      particles.forEach((p, i) => {
        if (
          p.passedFilter &&
          p.x > 600 &&
          p.x < 650 &&
          (p.y < 145 || p.y > 155)
        ) {
          return
        }

        if (p.x > 660) p.passedFilter = true

        if (
          (p.x < 600 && (p.y > 300 || p.y < 0)) ||
          (p.x > 600 && p.x < 650 && (p.y < 145 || p.y > 155)) ||
          p.x > 1000
        ) {
          setParticles(
            particles.filter((_p, j) => {
              return i !== j
            })
          )
        }

        let len = Math.sqrt(p.vx * p.vx + p.vy * p.vy)
        let x = p.vx / len
        let y = p.vy / len
        let B = 0
        let E = 0

        if (p.x >= 0 && p.x < 600) {
          //B =  0.005
          B = Bf
          //E =  0.01
          E = Ef
        } else if (p.x > 650) {
          B = Ba
          E = 0
        }
        let fa = (+len * B) / p.m

        let ax = y * fa
        let ay = -x * fa

        p.vx += ax
        p.vy += ay
        p.vy += E / p.m
        p.x += p.vx
        p.y += p.vy
      })
    }, 7)
    return () => clearInterval(interval)
  }, [particles, time, Bf, Ef, active, Ba])

  return (
    <>
      <Simulation
        time={time}
        particles={particles}
        Bf={Bf}
        Ba={Ba}
        Ef={Ef}
      ></Simulation>
      <div className="settingFilter setting">
        <input
          type="range"
          min="0"
          max="0.01"
          step="0.001"
          value={Ef}
          onChange={e => setEf(e.target.value)}
        ></input>
        {'\\( E_F = \\)'} {(Ef * 100).toPrecision(1)} {' \\( \\frac{V}{m} \\)'}
        <br />{' '}
        <input
          type="range"
          min="0"
          max="0.01"
          step="0.001"
          value={Bf}
          onChange={e => setBf(e.target.value)}
        ></input>
        {'\\( B_F = \\)'} {Bf} {' \\( T \\)'}
      </div>
      <div className="settingAnalysator setting">
      <input
        type="range"
        min="0"
        max="0.05"
        step="0.005"
        value={Ba}
        onChange={e => setBa(e.target.value)}
      ></input>
      {'\\( B_A = \\)'} {Ba} {' \\( T \\)'}
      </div>
      <div className="controls">
        <h1>Massenspektrometer</h1>
        <input
          type="button"
          onClick={() => setActive(!active)}
          value={!active ? 'Start' : 'Stop'}
        ></input>
        <input
          type="button"
          onClick={() => setParticles([])}
          value="ZurÃ¼cksetzen"
        ></input>
      </div>
    </>
  )
}

export default App
